<template>
  <div
    class="product-details-wrapper"
    align="center"
    v-if="productData.translated"
  >
    <HorizontalMenu
      :selectedMenuIten="defaultProductId"
      @selectedProductId="onProductIdCange($event)">
    </HorizontalMenu>
    <div class="top-pdp">
      <div class="md-layout">
        <div class="md-layout-item md-size-70 product-details-wrapper__product-section">
          <ImgPreview :imgData="mediaData"></ImgPreview>
          <div
            v-if="productData && contextData.currency"
            class="product-details-wrapper mobile-preorder-banner"
          >
            <PreOrderBanner
              :prodData="productData"
              :prodContext="contextData"
            />
          </div>
          <h3
            v-if="productData.translated && productData.translated.description"
            class="md-title mar-b-4 text-left"
            v-html="productData.translated.description"
          ></h3>
        </div>
        <div
          v-if="productData && contextData.currency"
          class="md-layout-item md-size-30 banner-column desktop-preorder-banner"
        >
          <PreOrderBanner
            :prodData="productData"
            :prodContext="contextData"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import ImgPreview from '../components/pdp/ImgPreview.vue';
import PreOrderBanner from '../components/pdp/PreOrderBanner.vue';
import HorizontalMenu from '../components/HorizontalMenu.vue';
import constants from '../constants';

const DEFAULT_LOCATION = 'CH';

export default {
  components: {
    ImgPreview,
    PreOrderBanner,
    HorizontalMenu,
  },
  data: () => ({
    isModalVisible: false,
    isExpanded: false,
    productData: {},
    contextData: {},
    mediaData: {},
    defaultProductId: '2ec99517afcb4d919dfce968bb32646b',
  }),
  created() {
    this.getGeolocation();
  },
  computed: {
    ...mapGetters(['getProductFromMenu']),
    selectedProduct() {
      return this.getProductFromMenu;
    },
  },
  methods: {
    async getGeolocation() {
      const location = await axios.get('http://77.245.20.254/geolocation');

      let newParams = constants.AVAILABLE_LOCATIONS[DEFAULT_LOCATION];

      if (location.data && constants.AVAILABLE_LOCATIONS[location.data.location]
      ) {
        newParams = constants.AVAILABLE_LOCATIONS[location.data.location];
      }

      if (!localStorage.getItem('contextToken')) {
        const newContextToken = await axios
          .patch('http://77.245.20.254/store-api/v3/context', {
            ...newParams,
          },
          {
            headers: {
              'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
            },
          });

        localStorage.setItem('contextToken', newContextToken.data.contextToken);
      }

      axios
        .get('http://77.245.20.254/store-api/v3/context', {
          headers: {
            'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
            'sw-context-token': localStorage.getItem('contextToken'),
          },
        })
        .then((response) => {
          this.contextData = response.data;
          // localStorage.setItem('isUserAuthorized', !!this.contextData.customer);
        });

      this.getProductData(this.defaultProductId);
    },
    getProductData(productId) {
      axios
        .post(`http://77.245.20.254/store-api/v3/product/${productId}`,
          {
            associations: {
              media: [],
            },
          },
          {
            headers: {
              'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
              'sw-context-token': localStorage.getItem('contextToken'),
            },
          })
        .then((response) => {
          this.productData = response.data.product;
          this.mediaData = this.productData.cover.media;
        });
    },
    onProductIdCange(productId) {
      this.defaultProductId = productId;
      this.getProductData(productId);
    },
  },
  watch: {
    selectedProduct(newValue) {
      if (newValue && newValue.id) {
        this.getProductData(newValue.id);
      }
    },
  },
};
</script>
